@use "global" as *;


.access {
  background: #FFF7EF;
  padding-top: rem(80);
  padding-bottom: rem(96.64);

  @include mq(md) {
    padding-top: rem(64);
    padding-bottom: rem(100);
  }
}

.access__inner {
  @include mq(md) {
    max-width: rem(calc(550 + 24.5 * 2));  // 両端のpadding分を考慮
    margin-inline: auto;
  }
}

.access__wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include mq(md) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.access__left {
  margin-top: rem(22);

  @include mq(md) {
    text-align: center;
    margin-top: 0;
  }
}

.access__title {
  @include mq(md) {
    margin-inline: auto;
  }
}

.access__info {
  margin-top: rem(30);
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);

  @include mq(md) {
    text-align: center;
    font-size: rem(14);
  }
}

.access__access {
  margin-top: rem(22);
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);

  @include mq(md) {
    text-align: center;
    font-size: rem(14);
  }
}

.access__right {

  @include mq(md) {
    margin-top: rem(40);
  }
}

.access__right iframe {  // Google Map
  aspect-ratio: 560 / 346;
  max-width: rem(560);
  width: rem(560);
  object-fit: cover;

  @include mq(md) {
    aspect-ratio: 345 / 213;
    max-width: rem(345);
    width: rem(345);
  }
}




// =======================================
// モディファイア
// =======================================
// access--2 (About ページ)
.access--2 {

}

.access--2 .access__title {
  display: none;
}

.access--2 .access__wrap {
  margin-top: rem(-66);
  align-items: flex-end;

  @include mq(md) {
    margin-top: 0;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }
}

.access--2 .access__left {
  padding-bottom: rem(22);

  @include mq(md) {
    width: 100%;
    padding-bottom: 0;
  }
}

.access--2 .access__right {

  @include mq(md) {
    margin-top: rem(32);
  }
}

.access--2 .access__info {
  @include mq(md) {
    margin-top: rem(25);
    text-align: left;
  }
}

.access--2 .access__access {
  @include mq(md) {
    text-align: left;
    font-size: rem(14);
  }
}

.access--2 .access__right iframe {  // Google Map
  aspect-ratio: 508 / 346;
  max-width: rem(508);
  width: rem(508);
  object-fit: cover;

  @include mq(md) {
    margin-top: 0;
    aspect-ratio: 345 / 213;
    max-width: rem(345);
    width: rem(345);
  }
}

.access__images {
  margin-top: rem(80);
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include mq(md) {
    margin-top: rem(40);
  }
}

.access__img {
  aspect-ratio: 1 / 1;
  max-width: rem(253.3);
  width: 100%;
  object-fit: cover;
  border-radius: 50%;
  overflow: hidden;

  @include mq(md) {
    max-width: rem(99);
    border-radius: 0;
  }
}

.access__img img {
  @include mq(md) {
    max-width: rem(99);
  }
}
