@use "global" as *;


.message {
  background: url(../images/common/message_bg-pc.webp) no-repeat center center / cover;
  padding-top: rem(80);
  padding-bottom: rem(80);

  @include mq(md) {
    background: url(../images/common/menu_bg-sp.webp) no-repeat center center / cover;
    padding-top: rem(64);
    padding-bottom: rem(64);
  }
}

.message__inner {
  @include mq(md) {
    max-width: rem(calc(550 + 24.5 * 2));  // 両端のpadding分を考慮
    margin-inline: auto;
  }
}

